 body {
   margin: 0;
   font-family: "Josefin Sans", sans-serif;
   /* font-family: 'Open Sans', sans-serif; */
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   font-size: 17px;
   padding: 0;
   box-sizing: border-box;
 }

 a {
   text-decoration: none;
   color: black;
 }

 a:hover {
   color: black;
 }

 i,
 a,
 p,
 h1,
 h2,
 h3,
 h4,
 h5,
 h6,
 span {
   padding: 0;
   margin: 0;
 }

 input:focus,
 select:focus,
 textarea:focus {
   outline: 0px;
 }

 .row,
 .col-6,
 .col-4 {
   margin: 0;
   padding: 0;
 }

 .Announcement {
   background-color: #f7ff07;
   padding: 10px 0px 6px 0px;
 }

 .Announcement .col-md-6 p {
   color: white;
   margin-right: 6rem;
   font-size: 15px;
 }

 .Announcement .col-12 a {
   color: white;
   margin-right: 2rem;
   font-size: 15px;
 }

 /* Header */
 .header {
   padding: 10px 0px 10px 0px;
 }

 .navbar-brand img {
   width: 60%;
 }

 .input-group {
   height: 50px;
   width: 90%;
 }

 .search-button {
   width: 20%;
   background-color: red !important;
   color: #f7ff07;
   border: 0;
   text-transform: uppercase;
   font-size: 12px;
 }

 .search-button:hover {
   -webkit-box-shadow: 0px 0px 15px 0px rgba(255, 0, 0, 1);
   -moz-box-shadow: 0px 0px 15px 0px rgba(255, 0, 0, 1);
   box-shadow: 0px 0px 15px 0px rgba(255, 0, 0, 1);
   z-index: 2;
   color: white;
 }

 .Login-Register a {
   text-transform: uppercase;
   font-size: 15px;
   margin-right: 2rem;
   position: relative;
 }

 .headerLeftSide {
   color: #c03f10 !important;
 }

 .Login-Register a:nth-last-child(1) {
   margin-right: 0rem;
 }

 .badge {
   background-color: #c03f10;
   position: absolute;
   top: -20px;
   border-radius: 100px;
   padding: 5px 7px;
   font-size: 12px;
 }

 .col {
   color: #c03f10;

 }

 .name-button {
   padding: 5px 15px;
   border: 1px solid #e4e4e4;
   background-color: white;
   border-radius: 2px;
   margin: 0 15px;
 }

 .dropdown-menu {
   background-color: black !important;
   border: 0 !important;
   padding: 0 !important;
   overflow: hidden;
 }

 .dropdown-menu a {
   padding: 10px 20px !important;
   color: #ffffff !important;
   margin: 0 !important;
 }

 .dropdown-menu a:hover {
   background-color: #f7ff07 !important;
   color: #c03f10 !important;
 }

 .mobile-header {
   display: none;
 }


 /* SHOP */
 .section {
   width: 100%;
   padding-bottom: 6%;
   padding-top: 2%;
 }

 .sidebar_item {
   margin-bottom: 35px;
 }

 .sidebar_item h4 {
   color: #1c1c1c;
   font-weight: bold;
   font-size: 24px;
   margin-bottom: 25px;
 }

 .sidebar_item ul {
   margin: 0;
   padding: 0;
 }

 .sidebar_item ul li {
   list-style: none;
   display: flex;
 }

 .sidebar_item ul li a {
   font-size: 16px;
   color: #000000;
   line-height: 45px;
   display: block;
   font-family: "Open Sans", sans-serif;
 }

 .sidebar_item ul li img {
   width: 25px;
   object-fit: contain;
   margin-left: 20px;
 }

 .shop {
   margin-bottom: 50px;
 }

 .border-product {
   padding: 15px;
   border: 1px solid #f3f3f3;
   border-radius: 5px;
 }

 .shopBack {
   height: 250px;
   position: relative;
   overflow: hidden;
   width: 100%;
   background-color: #f3fbf7;
 }

 .shopBack img {
   width: 100%;
   height: 250px;
   object-fit: contain;
 }

 .shoptext {
   padding-top: 10px;
 }

 .shoptext p a {
   color: #252525;
 }

 .shoptext h3 {
   color: #252525;
   margin-top: 13px;
   font-weight: bold;
   font-size: 19px;
 }

 .rating {
   margin: 5px 0;
 }

 .rating i {
   font-size: 12px;
   color: #ebb450;
 }

 .rating span {
   margin-left: 10px;
 }

 /* END OF SHOP */

 /* NEWSLATTER */
 .subscribe-section {
   background: linear-gradient(0deg, rgba(0, 0, 0, 0.226), rgba(0, 0, 0, 0.226)),
     url("https://images.pexels.com/photos/1312755/pexels-photo-1312755.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1") no-repeat scroll center / cover;
   padding: 100px 0 100px;
 }

 .subscribe-head h2 {
   color: #fff;
   font-size: 30px;
   letter-spacing: 0;
   line-height: 30px;
   margin: 0 0 13px;
   text-transform: uppercase;
 }

 .subscribe-head p {
   color: #fff;
   font-size: 18px;
   line-height: 22px;
   margin: 0 0 39px;
 }

 .subscribe-head {
   text-align: center;
 }

 .form-section input[type="email"] {
   border: 0 none;
   border-radius: 25px;
   color: #9b9b9b;
   font-size: 16px;
   font-style: normal;

   height: 51px;
   letter-spacing: 0;
   text-align: center;
   width: 460px;
 }

 .form-section input[type="submit"] {
   background: #f7ff07;
   border: 0 none;
   border-radius: 25px;
   color: #c03f10;
   height: 51px;
   position: relative;
   top: -1px;


   letter-spacing: 0;
   margin-left: -60px;
   padding: 0 58px;
   text-transform: uppercase;
   font-size: 12px;
 }

 .form-section input[type="submit"]:hover {
   box-shadow: 1px 1px 5px 1px #f7ff07;
 }

 /* CONTACT */
 .contactInfo {
   padding: 70px 0;
 }

 .contact-Box,
 .box-info,
 .info-image {
   display: flex;
   justify-content: center;
   align-items: center;
 }

 .box-info {
   width: 80%;
   flex-direction: column;
   padding: 40px 0;
   box-shadow: 0px 0px 27px 0px rgb(243, 243, 243);
 }

 .info-image {
   width: 50px;
   height: 50px;
   border: 2px solid #f7ff07;
   border-radius: 100px;
   font-size: 20px;
   color: #c03f10;

 }

 .box-info h5 {
   font-weight: bold;
   font-size: 19px;
   margin-top: 20px;
   margin-bottom: 4px;
 }

 .box-info p {
   font-size: 16px;
 }

 /* SINGLE PRODUCT */
 .single-product {
   margin-top: 50px;
   margin-bottom: 50px;
 }

 .single-image {
   background-color: #f3fbf7;
   margin-right: 20px;
   height: 700px;
   display: flex;
   justify-content: center;
   align-items: center;
 }

 .single-image img {
   width: 90%;
   height: 700px;
   object-fit: contain;
 }

 .product-name {
   font-size: 25px;
   font-weight: 600;
   line-height: 22px;
   margin-bottom: 30px;
 }

 .display-flex {
   display: flex;
 }

 .product-info {
   width: 100%;
 }

 .product-dtl p {
   line-height: 24px;
   color: #7a7a7a;
 }

 .product-count {
   border: 2px solid #f3f3f3;
   border-radius: 5px;
   margin-top: 40px;
 }

 .product-count .flex-box {
   padding: 15px 25px;
   border-bottom: 2px solid #f3f3f3;
 }

 .product-count .flex-box h6 {
   font-size: 16px;
 }

 .product-count .flex-box span {
   font-weight: 600;
   color: #000000;
 }

 .product-count .flex-box select {
   background-color: #f3f3f3;
   width: 100px;
   height: 40px;
   text-align: center;
   border: 0px;
   border-radius: 5px;
 }

 .round-black-btn {
   border-radius: 4px;
   background: #f7ff07;
   color: #c03f10;
   width: 100%;
   height: 50px;
   border: 0;
   text-transform: uppercase;
   font-weight: bold;
   font-size: 15px;
   transition: all 0.5s ease-in-out 0s;
 }

 .round-black-btn:hover,
 .round-black-btn:focus {
   background: #f7ff07;
 }

 .outOfStockMessage {
   color: red !important;
 }

 /* LOGIN */
 .login-center {
   padding: 50px 0;
 }

 .Login,
 .Login2 {
   background: #fff;
   border-radius: 5px;
   position: relative;
   padding: 35px 30px 30px;
   box-shadow: 0 1px 11px rgba(168, 168, 168, 0.27);
   text-align: center;
 }

 .Login input {
   width: 100%;
   padding: 20px 20px;
   margin-top: 20px;
   border: 1px solid #e4e4e4;
   border-radius: 5px;
   color: #8a8a8a;
 }

 .Login input:focus {
   outline: 0px;
 }

 .Login button,
 .Login2 button {
   width: 100%;
   margin-top: 20px;
   padding: 20px 0;
   border: 0;
   background-color: #f7ff07;
   border-radius: 5px;
   text-transform: uppercase;
   color: #c03f10;

 }

 .Login button a,
 .Login2 button a {
   text-transform: uppercase;
   color: #c03f10;

 }

 .Login p {
   margin-top: 30px;
 }

 .Login p a {
   color: #c03f10;

 }

 /* PROFILE */
