 /* POPUP */
 .popupModal {
   font-size: 15px;
 }

 .popupHeader {
   width: 100%;
   border-bottom: 1px solid gray;
   font-size: 18px;
   text-align: center;
   padding: 5px;
   color: red;
 }

 .popupContent {
   width: 100%;
   padding: 10px 5px;
   text-align: center;
 }

 .popupActions {
   width: 100%;
   padding: 10px 5px;
   margin: auto;
   text-align: center;
 }

 .popupClose {
   cursor: pointer;
   position: absolute;
   display: block;
   padding: 2px 5px;
   line-height: 20px;
   right: -10px;
   top: -10px;
   font-size: 24px;
   background: #ffffff;
   border-radius: 18px;
   border: 1px solid #cfcece;
   color: red;
