   border: none;
   color: black;
 }

 .popupButton:hover {
   box-shadow: rgb(0, 255, 0);
   -webkit-box-shadow: 0px 0px 17px 0px rgba(0, 255, 64, 1);
   -moz-box-shadow: 0px 0px 17px 0px rgba(0, 255, 64, 1);
